@import "helpers/variables";
@import "helpers/mixins";
@import "helpers/functions";
@import "vendor/libs";
@import "base/fonts";
@import "base/general";
@import "../blocks/modules";

$accentColor: #65e496;
$lightColor: #fff;
$darkText: #696980;
$darkTitle: #44444f;
$bg-light: #f5f6fa;
$bg-dark: #44444f;

ul,
li {
    display: block;
    padding: 0;
    margin: 0;
}

button {
    border: none;
    border-color: none;
    border-image: none;
    border-width: none;
    margin: 0;
    padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: 700;
    font-family: 'Roboto Slab', serif;
    color: $darkTitle;
}

h2 {
    font-size: 48px;
    text-align: center;
    letter-spacing: 1.5px;
    color: $darkTitle;

    span {
        color: $accentColor;
        font-size: 18px;
        font-weight: 400;
        display: block;
        margin-top: 11px;
        letter-spacing: 1px;
    }
}

h4 {
    font-size: 24px;
    letter-spacing: 1px;
    color: $darkTitle;
}

.wrap {
    max-width: 1170px;
    margin: 0 auto;
}

a,
a:hover {
    text-decoration: none;
    color: #fff;
}


body {
    font-family: 'Roboto Slab', serif;
    overflow-x: hidden;
    color: $darkText;
}

p {
    font-family: 'Open Sans', sans-serif;
}

.btn {
    background: rgba(101, 228, 156, 1);
    background: -moz-linear-gradient(left, rgba(101, 228, 156, 1) 0%, rgba(101, 228, 175, 1) 100%);
    background: -webkit-gradient(left top, right top, color-stop(0%, rgba(101, 228, 156, 1)), color-stop(100%, rgba(101, 228, 175, 1)));
    background: -webkit-linear-gradient(left, rgba(101, 228, 156, 1) 0%, rgba(101, 228, 175, 1) 100%);
    background: -o-linear-gradient(left, rgba(101, 228, 156, 1) 0%, rgba(101, 228, 175, 1) 100%);
    background: -ms-linear-gradient(left, rgba(101, 228, 156, 1) 0%, rgba(101, 228, 175, 1) 100%);
    background: linear-gradient(to right, rgba(101, 228, 156, 1) 0%, rgba(101, 228, 175, 1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#65e49c', endColorstr='#65e4af', GradientType=1);
    border-radius: 4px;
    max-width: 250px;
    font-family: 'Roboto Slab', serif;
    display: block;
    text-align: center;
    font-size: 18px;
    letter-spacing: 0.8px;
    padding: 13px 0;
    border-bottom: 2px solid #59bc88;
    border-top: 2px solid transparent;
    outline: none;


    &:hover {
        border-bottom: 2px solid $accentColor;
    }

    &:active {
        color: #51a19f;
        border-top: 2px solid #59bc88;
    }
}



.header {
    width: 100%;
    background: url("../img/bg%20lines.jpeg") no-repeat top/ cover;

}

.header-nav {
    max-width: 1170px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    padding-top: 50px;
    padding: 50px 0 0;

    &__logo img {
        display: block;
    }
}

.header-nav-block {
    text-decoration: none;
    display: flex;
    justify-content: flex-end;


    &__item {
        list-style-type: none;
        margin-left: 20px;
        font-family: 'Open Sans', sans-serif;
        letter-spacing: 0.8px;
        color: $lightColor;
        font-size: 16px;
        font-weight: 400;
        border-bottom: 3px solid transparent;
        padding: 5px 0 5px 0;
        transition: 0.3s;

        &:hover {
            border-bottom: 3px solid $accentColor;
        }

        .item-link {
            text-decoration: none;
            color: $lightColor
        }

    }

    &__item.active {
        border-bottom: 3px solid $accentColor;
    }

}

.header-main {
    max-width: 1170px;
    display: flex;
    margin: 0 auto;
    margin: 178px auto 0 auto;
    padding-bottom: 160px;
}

.header-main-book {
    position: relative;

    img {
        border-radius: 8px;
    }

    &__title {
        position: absolute;
        top: 11.5%;
        right: 21%;
        font-size: 36px;
        font-family: 'Roboto Slab', serif;
        text-transform: uppercase;
        letter-spacing: 2px;
        color: $lightColor;
    }

    &__under {
        color: $accentColor;
        position: absolute;
        top: 24.5%;
        right: 21%;
        font-size: 14px;
        font-family: 'Open Sans', sans-serif;
        letter-spacing: 0.7px;
    }

    &__author {
        color: $accentColor;
        position: absolute;
        bottom: 9%;
        right: 47.5%;
        font-size: 14px;
        font-family: 'Open Sans', sans-serif;
        letter-spacing: 0.7px;
    }
}

.header-main-block {
    margin: 1px 0 0 53px;
    color: $lightColor;

    &__author {
        font-size: 18px;
        color: $lightColor;
        letter-spacing: 0.6px;
        margin-left: 2px;
    }

    &__title {
        font-size: 48px;
        font-weight: 700;
        text-transform: uppercase;
        margin-top: 30px;
        letter-spacing: 1.45px;
        color: $lightColor;

        span {
            color: $accentColor;
            font-weight: 400;
            font-size: 18px;
            text-transform: none;
            display: block;
            letter-spacing: 1px;
            margin: 11px 0 0 1px;
        }

    }

    &__about {
        font-family: 'Open Sans', sans-serif;
        font-weight: 400;
        font-size: 15px;
        max-width: 580px;
        margin-top: 41px;
        line-height: 1.6;
        letter-spacing: 0.75px;

    }

    &__btn {
        margin-top: 46px;
    }

    small {
        margin-top: 22px;
        display: block;
        font-style: italic;
        letter-spacing: 0.1px;
        font-size: 12px;
    }


}

.header-main-block p:nth-child(3) {
    margin-bottom: -18px;
}

.author {
    max-width: 1170px;
    margin: 159px auto 0 auto;
    display: flex;
    justify-content: space-between;
    letter-spacing: 0.75px;
    color: $darkText;
}

.author-text {
    margin-top: 16.5px;
    flex-basis: calc(93% - 500px);

    &__author {
        font-size: 18px;
    }

    &__name {
        font-size: 48px;
        letter-spacing: 1.5px;
        margin-top: 19px;
        text-align: left;
        color: $darkTitle;

        span {
            display: block;
            color: $accentColor;
            font-size: 18px;
            font-weight: 400;
            letter-spacing: 1px;
            margin-top: 11px;
        }
    }

    &__about {
        margin-top: 42px;
        font-size: 15px;
        max-width: 600px;
        line-height: 1.6;
        letter-spacing: 0.75px;
    }

    p:nth-child(3) {
        margin-bottom: -20px;
    }

    &__btn {
        max-width: 237px;
        margin-top: 47px;
    }
}

.author-photo {
    position: relative;

    &__svg {
        z-index: 0;
    }

    &__photo {
        position: absolute;
        z-index: 1;
        left: -10.8%;
        bottom: 8.3%
    }
}

.counter {
    margin: 135px 0 165px 0;
    display: flex;
    justify-content: space-around;

    span {
        display: block;
        text-align: center;
        letter-spacing: 0.7px;
        font-size: 18px;

        &:nth-child(1) {
            font-size: 80px;
            font-weight: 100;
            color: #65e49d;
            letter-spacing: 3px;
        }

        &:nth-child(2) {
            margin-top: 15px;
        }
    }
}

.content {
    background-color: $bg-light;

    .wrap {
        padding-top: 142px;
        padding-bottom: 255px;
    }

    &-block {
        display: flex;
        justify-content: space-between;
        max-width: 1170px;
        margin: 0 auto;

        &-about {
            flex-basis: 40%;
            margin-top: 84px;

            p {
                margin-top: 20px;
                letter-spacing: 0.25px;
                line-height: 24px;
            }

            &__icons {
                margin-top: 31px;
                font-weight: 700;
                font-size: 18px;
                letter-spacing: 1.2px;

                &-link {
                    margin-left: 18px;
                }

                & > a i {
                    color: $accentColor;
                    font-size: 18px;
                    margin-right: 10px;
                }

            }
        }

        &-table {
            flex-basis: 49%;

            h4 {
                margin: 85px 0 27px 0;
            }

            &-list {
                margin: 0;
                padding: 0;
                counter-reset: list;
                column-count: 2;
                column-gap: 23px;

                & > li {
                    position: relative;
                    padding-left: 37px;
                    font-family: 'Open Sans', sans-serif;
                    font-size: 15px;
                    letter-spacing: 1px;
                    counter-increment: list;
                    list-style-type: none;
                    letter-spacing: 0.8px;
                    margin-bottom: 15px;

                    &::before {
                        content: counter(list) ". ";
                    }

                    &::after {
                        content: "+";
                        font-family: 'Open Sans', serif;
                        font-size: 15px;
                        line-height: 21px;
                        color: $lightColor;
                        background-color: #5fc9ad;
                        top: 0px;
                        padding: 0px 7px;
                        position: absolute;
                        left: 1px;
                    }
                }
            }

            &__btn {
                width: 135px;
                margin: 25px 0 0 3px;
                color: $lightColor;
                letter-spacing: .75px;
                cursor: pointer;
                outline: 0;
            }
        }
    }
}

.other {
    padding: 145px 0;
    margin: 0 auto;
    max-width: 1170px;
    display: flex;
    flex-flow: column wrap;
    align-items: center;

    &-books {
        display: flex;
        justify-content: space-between;
        text-align: center;

        &-item {
            display: flex;
            flex-flow: column wrap;
            align-items: center;
            margin-top: 87px;

            h4 {
                letter-spacing: 1px;
            }

            p {
                line-height: 24px;
                font-family: 'Open Sans', sans-serif;
                font-size: 15px;
                letter-spacing: .8px;

            }

            a {
                color: $accentColor;
                text-transform: uppercase;
                font-weight: 700;
                font-family: 'Open Sans', sans-serif;
                margin-top: 5px;
                letter-spacing: 0.75px;
            }

            &:nth-child(2) {
                margin-left: 27px;
                margin-right: 27px;
            }

            &__cover {
                background: url(../img/smallBook.jpeg) no-repeat center;
                width: 269px;
                height: 409px;
                position: relative;
                margin-bottom: 35px;

                h4 {
                    font-weight: 700;
                    font-size: 24px;
                    text-transform: uppercase;
                    text-align: center;
                    color: $lightColor;
                    margin: 70px 0 0 36px;
                }

                span {
                    display: block;
                    font-size: 12px;
                    color: $accentColor;
                    margin: 10px 0 0 31px;
                    letter-spacing: 0.65px;
                }

                small {
                    direction: block;
                    font-size: 12px;
                    color: $accentColor;
                    position: absolute;
                    bottom: 8%;
                    left: 30%;
                    letter-spacing: 0.095px;
                }

            }
        }
    }

    &__btn {
        width: 220px;
        margin-top: 87px;
    }
}

.events {
    max-width: 1170px;
    margin: 159px auto 0 auto;
    display: flex;
    justify-content: space-between;
    letter-spacing: 0.75px;
    color: $darkText;
    padding-bottom: 178px;
}

.events-text {
    margin-top: 17px;
    flex-basis: 50%;

    &__author {
        font-size: 18px;
        letter-spacing: .9px;

    }

    &__name {
        font-size: 48px;
        letter-spacing: 1.7px;
        margin-top: 18px;
        text-align: left;
        color: $darkTitle;

        span {
            display: block;
            color: $accentColor;
            font-size: 18px;
            font-weight: 400;
            letter-spacing: 1.14px;
            margin-top: 11px;
        }
    }

    &-date {
        margin-top: 43px;
        font-size: 15px;
        line-height: 1.6;

        span {
            letter-spacing: 1.15px;
            margin-bottom: 3px;
            display: block;
        }

        h5 {
            font-size: 18px;
            font-weight: 700;
            font-family: 'Roboto Slab', sans-serif;
            letter-spacing: .9px;

        }

        p {
            letter-spacing: .75px;
            max-width: 98%;
            margin-bottom: 22px;
            margin-top: 13px;
        }

        a {
            max-width: 139px;
            padding: 9px 0;
        }
    }
}

.events-text-date:nth-child(3) {
    margin-bottom: 56px;
}

.events-photo {
    position: relative;
    flex-basis: 52.3%;

    &__svg {
        z-index: 0;
        margin-top: 18px;
    }

    &__photo {
        position: absolute;
        z-index: 1;
        left: 8.5%;
        top: 9.2%;
    }
}

.blog {
    background-color: $bg-light;

    .wrap {
        padding: 148px 0 160px 0;
        max-width: 1170px;
        margin: 0 auto;
        text-align: center;
        display: flex;
        flex-flow: column wrap;
        align-items: center;
    }

    &-title {
        margin: 0;
        padding: 0;
    }

    &-list {
        display: flex;
        justify-content: space-between;
        margin: 87px 0 67px 0;

        &-item {
            max-width: 369px;
            text-align: left;

            &:nth-child(2) {
                margin: 0 31px;
            }

            h3 {
                margin-top: 13px;
                letter-spacing: 0.2px;
                margin-bottom: 8px;
            }

            span {
                font-size: 15px;
                font-family: 'Open Sans', sans-serif;

                span {
                    color: $accentColor;
                    letter-spacing: .75px;
                }
            }

            p {
                line-height: 1.6;
                font-size: 15px;
                letter-spacing: .75px;
                font-family: 'Open Sans', sans-serif;
            }
        }
    }

    &__btn {
        max-width: 285px;
        width: 265px;
    }
}

.join {
    display: flex;
    justify-content: space-between;
    max-width: 1170px;
    margin: 0 auto;
    padding: 153px 0 138px;

    &-news {
        text-align: left;
        flex-basis: calc(100% - 570px);

        h2 {
            text-align: left;
        }

        p {
            line-height: 1.5;
            margin-top: 10px;
            letter-spacing: 0.3px;
        }
    }

    &-form {
        margin-top: 10px;
        flex-basis: 52%;
        margin-left: 40px;

        &__input {
            width: 100%;
            background-color: $bg-dark;
            padding: 15px 0;
            border-radius: 3px;
            border: none;
            outline: none;
            box-sizing: border-box;

            &::placeholder {
                padding-left: 25px;
                letter-spacing: .1px;
                color: #9191b5;
            }

            &:focus {
                outline: none;
                color: #9191b5;
                padding-left: 25px;
                opacity: 1;

                &:placeholder-shown {
                    opacity: 0;
                    background-color: $bg-dark;
                }
            }
        }

        &__btn {
            margin-top: 20px;
            color: $lightColor;
            max-width: 570px;
            width: 100%;
            cursor: pointer;
        }
    }
}

.footer {
    background-color: $bg-dark;

    &-links {
        padding: 160px 0 145px;
        display: flex;
        justify-content: space-between;
        max-width: 1170px;
        margin: 0 auto;
        color: #9191b5;

        &-about {
            flex-basis: 40%;

            p {
                margin-top: 24px;
                max-width: 371px;
                line-height: 1.5;
                letter-spacing: .14px;
                margin-bottom: 26px;
            }

            span {
                letter-spacing: 0.14px;
                font-family: 'Open Sans', sans-serif;
            }
        }

        &-useful {
            text-align: left;
            flex-basis: 24.5%;
            margin-top: 10px;

            h4 {
                color: $accentColor;
                text-transform: uppercase;
                font-size: 18px;
                letter-spacing: .6px;
                margin-bottom: 26px;
            }

            ul {
                li {
                    margin-bottom: 11px;
                    letter-spacing: 0.6px;
                    line-height: 1.16;
                    font-size: 15px;
                    font-weight: 400;
                    font-family: 'Open Sans', sans-serif;

                    a {
                        color: #9191b5;
                    }
                }
            }
        }

        &-contacts {
            flex-basis: 30%;
            margin-top: 10px;
            width: 60%;
            display: flex;
            flex-flow: column wrap;
            align-items: flex-start;

            h4 {
                color: $accentColor;
                text-transform: uppercase;
                font-size: 18px;
                letter-spacing: .6px;
                margin-bottom: 26px;
                padding-left: 80px;
            }

            &__cont {
                padding-left: 80px;
                margin-bottom: 10px;

                li {
                    margin-bottom: 10px;
                    line-height: 1;
                    font-size: 15px;
                    letter-spacing: .6px;
                    font-family: 'Open Sans', sans-serif;

                    a {
                        color: #9191b5;

                    }
                }
            }

            &__socials {
                display: flex;
                padding-left: 80px;

                li {
                    margin-right: 13px;

                    a {
                        i {
                            color: $accentColor;
                            font-weight: 400;
                        }
                    }
                }

            }
        }
    }
}

.expert {
    text-align: center;
    background-color: $bg-dark;

    .wrap {
        padding: 150px 0;
    }

    &-slider {
        &-item {
                display: flex;
                position: relative;
            &__img {
                display: block;
                margin: 0 auto;
                position: relative;
                overflow: hidden;
                width: 152px;
                height: 152px;
                border-radius: 100%;
                img {
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%, -50%);
                    object-fit: cover;
                    width: 152px;
                    height: 152px;
                }
            i{
                    color: red;
                    position: absolute;
                }
            }
            
        }
    }
}
